import { AppBar } from 'components/AppBar';
import { Navigation } from 'components/Navigation';
import { Balance } from 'components/Balance';
import { Currency } from 'components/Currency';
import { Transactions } from 'components/Transactions';
import { Statistics } from 'pages/Statistics/Statistics';
import Media from 'react-media';
import { useState } from 'react';
import { useLocation } from 'react-router-dom';
// import { Container } from 'globalStyles/globalStyle';
import {
  // Wrapper,
  // LeftSide,
  // RightSide,
  PageContainer,
  Wrap,
  AddTransButton,
  SvgAddBtn,
  DashboardSeparator,
} from './DashboardPage.styled';
import AddTransactionModal from 'components/Transactions/AddTransactionModal/AddTransactionModal';

const Dashboard = () => {
  const { pathname } = useLocation();
  const [open, setOpen] = useState(false);

  const handleModalToggle = () => {
    setOpen(true);
  };

  const handleButtonToggle = () => {
    setOpen(false);
  };

  return (
    <>
      <AppBar />
      <PageContainer>
        <Media
          query="(max-width: 767px)"
          render={() => (
            <>
              <Navigation />
            </>
          )}
        />
        <DashboardSeparator pathname={pathname}></DashboardSeparator>
        {pathname === '/statistic' && <Statistics />}
        {pathname === '/home' && (
          <>
            {open ? (
              <AddTransactionModal handleButtonToggle={handleButtonToggle} />
            ) : (
              <Wrap>
                <AddTransButton type="button" onClick={handleModalToggle}>
                  <SvgAddBtn />
                </AddTransButton>
              </Wrap>
            )}
            <Balance />

            <Transactions
            // onDelete={handleDelete}
            />
          </>
        )}
        {pathname === '/currency' && <Currency />}

        {/* <Wrapper>
          <LeftSide>
            <Navigation />
            <Balance />
            <Currency />
          </LeftSide>
          <RightSide>
            <Transactions />
          </RightSide>
        </Wrapper> */}
      </PageContainer>
    </>
  );
};

export default Dashboard;
